export default {
    name: 'Accordion',

    data() {
        return {
            show: false,
            timeout: 0,
            animate: false
        }
    },
    methods: {
        toggle: function() {
            if (this.show == false) {
                this.clearT()
            }
            this.show = !this.show;
        },
        beforeEnter: function(el) {
            el.style.height = '0';
        },
        enter: function(el) {
            el.style.height = el.scrollHeight + 'px';
        },
        beforeLeave: function(el) {
            el.style.height = el.scrollHeight + 'px';
        },
        leave: function(el) {
            el.style.height = '0';
        },
        toggleH: function() {  
            this.timeout = setTimeout(() => ( this.show = true, this.animate = false ), 2000);
            if (this.show == false) {
                this.animate = true;
            }
        },
        clearT: function() {
            clearTimeout(this.timeout);
            this.animate = false;
        },
    }
}        

